<template>
  <div id="notFound" :class="{hasHeader: $utils.showNavBar()}">
    <HeaderNav v-if="$utils.showNavBar()"></HeaderNav>
    <div class="imgBox">
      <img src="../../assets/img/404_icon.png" alt="">
    </div>
    <div class="clear index" @click="$router.push({path:'/'})">
      <div class="fl indexImg">
        <img src="../../assets/img/404_close.png" alt="">
      </div>
      <div class="fl txt">返回首页</div>
    </div>
    <div class="clear recommend pB10">
      <div class="fl pB20 f36 color50">
        精彩推荐
      </div>
    </div>
    <news-list
      :list="list"
      class="wonderful"></news-list>
  </div>
</template>

<script>

import NewsList from '../common/news-list'
import HeaderNav from '@/components/task/header-nav'

export default {
  name: 'notFound',
  data () {
    return {
      list: []    // 精彩推荐列表
    }
  },
  methods: {
    // 获取精彩推荐列表
    getRecommend () {
      let data = {}
      data.limit = 20    // 拿20条
      this.$api.get('AID_RECOMMEND', data, (res) => {
        this.list = res.data || []
      })
    }
  },
  mounted () {
    this.getRecommend()
  },
  components: {
    NewsList,
    HeaderNav
  }
}
</script>

<style lang="scss" scoped>
#notFound{
  .index{
    line-height: 40px;
    font-size: 34px;
    margin-top: 30px;
    .txt{
      color: #FFA600;
    }
  }
  .indexImg{
    width: 40px;
    height: 40px;
    margin-right: 10px;
    margin-left: 280px;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .imgBox{
    width: 100%;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .recommend{
    padding-left: 34px;
    margin-top: 68px;
    div{
      border-bottom: 1px solid #FBAA03;
    }
  }
}
</style>



